import Header from './component/Header';
import Footer from './component/footer';
import Team from './component/Team';
import Data from './data.json';

function App() {
    let myElement;
    myElement = Data.map((item, index) => <Team key={index} MemberName={item.MemberName} Designation={item.Designation} Company={item.Company} Country={item.Country} />);

    return (
        <div>
            <h1 className="headerTitle">Simple React App</h1>
            <Header />
            {myElement}
            <Footer />
        </div>
    );
}

export default App;