
function Team(props) {
    const { MemberName, Designation, Company, Country } = props;
    return (
        <div className="team">
            <div className="team-member">
                <h3 className="member-name">{MemberName}</h3>
                <label>Designation:</label><h5 className="designation"> {Designation}</h5>
                <label>Company:</label><h5 className="company"> {Company}</h5>
                <label>Country:</label><h5 className="country"> {Country}</h5>
            </div>
        </div>
    );
}

export default Team;